import { request,getToken,Token } from '../request.js'
export default {
	data() {
		return {
			is_boss:0,
			qrcode:'',
		}
	},
	onLoad(options){
		if(typeof options.uid!='undefined'){
			let uid = options.uid;
			uni.showModal({
				content: '确定要成为该合作伙伴的员工吗？',
				success: res => {
					if(res.confirm) {
						request({
							url:'index.php?s=/wap/member/bindStaff',
							method:"POST",
							data:{uid:uid},
						}).then(res=>{
							uni.showToast({
								title:res.data.msg,
								icon: 'none',
							})
							if(res.data.code==1){
								setTimeout(function(){
									uni.switchTab({
										url:"/pages/mine/mine"
									})
								},1000);
							}
						});
					}
				}
			});
		}else{
			this.is_boss = 1;
			this.getQrcode();
		}
	},
	methods:{
		getQrcode(){
			let that = this;
			request({
				url:"index.php?s=/wap/member/getBossQrcode",
				data:{
					plat:'wap',
				}
			}).then(function(res){
				that.qrcode = res.data;
			});
		}
	}
}
